import React, {useEffect, useState} from 'react';
import './App.css';
import Header from './components/Header';
import IndexList from './pages/IndexList';
import {getGroup, getGroupIds} from './services/API';

export const Context = React.createContext();

function App() {
  const [loading, setLoading] = useState(true);
  const [group, setGroup] = useState([]);

  useEffect(() => {
    try {
      let res = [];
      getGroupIds()
        .then((data) => {
          setGroup(data.map((id) =>
            getGroup(id).then((data) => {
              // res.push(data);
              console.log(data, 'data from app');
              
            })
          ));
        })
        .finally(() => setLoading(false));
    } catch (err) {
      console.log(`Something went wrong: ${err}`);
    }
  }, []);

  console.log(group, 'group from app');

  if (loading) {
    return <div>LOADING...</div>;
  }

  return (
    <Context.Provider value={{group}}>
      <div className="App">
        <Header />
        <IndexList />
      </div>
    </Context.Provider>
  );
}

export default App;
